<template>
  <div class="dialog">
    <el-dialog :title="title" :visible.sync="isDisplay" @close="cancel" @close-on-click-modal="false" width="30%">
      <div class="description">{{description}}</div>
      <span slot="footer" class="dialog-footer">
        <el-button type="warning" @click="cancel">取 消</el-button>
        <el-button type="primary" @click="confirm"
          >确 定</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      required: true,
    },
    description: {
      type: String,
      required: true,
    },
    isDisplay: {
      type: Boolean,
      default: false,
    },
  },
  data(){
      return{
          dialogDisplay:false
      }
  },
  watch: {
      isDisplay(){
          this.dialogDisplay = this.isDisplay;
      }
  },
  methods:{
      cancel(){
          this.$emit('cancel');
      },
      confirm(){
          this.$emit('confirm');
      }
  }
};
</script>

