<template>
  <div class="authority">
    <el-row class="header">
      <el-col :span="4">
        <el-input
          placeholder="请输入内容"
          suffix-icon="el-icon-search"
          v-model="userQuery"
        >
        </el-input>
      </el-col>
      <el-button>搜素</el-button>
      <el-button>添加人员</el-button>
    </el-row>
    <el-table
      :data="tableData"
      style="width: 100%"
      header-cell-class-name="tableHeader"
      cell-class-name="tableHeader"
      row-class-name="tableRow"
      :highlight-current-row="true"
      height="550"
    >
      <el-table-column fixed prop="username" label="用户名"> </el-table-column>
      <el-table-column prop="position" label="职位"></el-table-column>
      <el-table-column prop="authority" label="权限"></el-table-column>
      <el-table-column fixed="right" label="操作" width="200">
        <template slot-scope="scope">
          <el-button type="primary" :disabled="scope.row.isEdit" icon="el-icon-edit" size="small" circle @click="edit(scope.row)"></el-button>
          <el-button type="danger" @click="deleteDialogDisplay=true;deleteIndex=scope.row.id "  :disabled="scope.row.isDelete" icon="el-icon-delete" size="small" circle ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 编辑弹框 -->
    <el-dialog
      title="编辑信息"
      :visible.sync="isEditDisplay"
      width="40%"
      center
      class="edit-dialog"
    >
    <div class="edit-content">
        <div class="left">用户名</div>
        <div class="right">
          <el-input v-model="selectItem.username"></el-input>
        </div>
    </div>
    <div class="edit-content">
        <div class="left">职位</div>
        <div class="right">
         <el-select v-model="selectItem.position">
             <el-option v-for="(item,index) in positionOptions" :value="item.title" :label="item.title" :key="index"></el-option>
         </el-select>
        </div>
    </div>
    <div class="edit-content">
        <div class="left">权限</div>
        <div class="right">
          {{selectItem.authority}}
        </div>
    </div>
    <div slot="footer"  class="btns">
      <el-button type="primary">
              保存
          </el-button>
          <el-button type="warning">
              取消
          </el-button>
    </div>
    </el-dialog>
    <!-- 删除弹框 -->
     <Dialog title="删除确认" @cancel="deleteDialogDisplay=false"  description="确定要删除所选的用户吗？" @confirm="deleteInfo(deleteIndex)" :isDisplay.sync="deleteDialogDisplay"></Dialog>
  </div>
</template>

<script>
import Dialog from "components/Dialog.vue"

export default {
  data() {
    return {
      userQuery: "",
      isEditDisplay:false,
      deleteDialogDisplay:false,
      deleteIndex:-1,
      selectItem:{},
      tableData: [
        {
          username: "王小虎",
          position: "管理人员",
          authority: "编辑、删除",
          isEdit: true,
          isDelete: true,
          id:0
        },
        {
          username: "唐晓娜",
          position: "操作人员",
          authority: "编辑",
          isEdit: false,
          isDelete: false,
          id:1
        },
        {
          username: "刘梦洁",
          position: "测试人员",
          authority: "预览",
          isEdit: false,
          isDelete: false,
          id:2
        },
      ],
      positionOptions:[{
          title:'操作人员'
      },
      {
          title:'管理人员'
      },
      {
          title:'测试人员'
      },
      {
          title:'技术人员'
      },
      ]
    };
  },
  components:{
      Dialog
      },
  methods: {
    tableHeader() {
      return 'tableHeader';
    },
    edit(item){
        this.isEditDisplay=true;
        this.selectItem = item;
    },
    deleteInfo(index){
      this.tableData.splice(index,1);
      this.tableData.map((item,index)=>{
          item.id = index
      })
      this.deleteDialogDisplay=false;
    }
  },
};
</script>

<style scoped>
.header {
  text-align: left;
  margin-bottom: 20px;
}
.header .el-button {
  background-color: #539dff;
  color: aliceblue;
  width: 100px;
  margin: 0 10px;
}
.tableHeader {
  text-align: center;
}
.edit-content{
    display: flex;
    line-height: 40px;
}
.edit-content>div{
    margin-bottom: 20px;
}
.left{
    width:100px;
    font-weight: bold;
}
.right{
    margin-left:40px;
    width:300px;
}
.el-select{
    width:100%;
}
.btns{
    margin-top:40px;
    text-align: right;
}
</style>